import React from 'react';
import { Link } from 'react-router-dom';
import './index.css';
import ReactHighcharts from 'react-highcharts/ReactHighstock.src';

class Home extends React.Component {
	render() {

		var chartData = [];

		for(var i=1;i<=300;i++){
			var date = new Date()
			var result = date.setDate(date.getDate() + i);
		    var num = Math.round(Math.random()*100);
		    var arr = [];

		    var currentDate = new Date(result).getDate();
		    var currentMonth = new Date(result).getMonth() + 1;
		    var currentYear = new Date(result).getFullYear();
		    var newDate = ((currentDate<10)? '0'+currentDate : currentDate) + '-' + ((currentMonth<10)? '0'+currentMonth : currentMonth) + '-' + currentYear;

		    arr.push(result);
		    arr.push(num);
		    chartData.push(arr);
		}

		var config = {
			xAxis: {
			  labels: {
			    enabled: false
			  },
			  minRange: 3600 * 1000
			},
			credits: {
				enabled: false
			},
		    tooltip: {
		        positioner: function () {
		            return { x: 80, y: 50 };
		        },
		        formatter: function () {
		        	var date = new Date(this.x).getDate() + '/' + (new Date(this.x).getMonth() + 1)  + '/' + new Date(this.x).getFullYear();

		            return '<b>Date:</b>' + date  + '<br/>' + '<b>Stock:</b>' + this.y + '</b>';
		        },
		        shadow: false,
		        borderWidth: 0
		    },
			rangeSelector: {
				selected: 1,
				inputEnabled: false,
				buttons: [
					{
					    type: 'day',
					    count: 1,
					    text: '1d'
					}, 
					{
					    type: 'day',
					    count: 7,
					    text: '7d'
					}, 
					{
					    type: 'month',
					    count: 1,
					    text: '1m'
					}, 
					{
					    type: 'month',
					    count: 6,
					    text: '6m'
					},  
					{
					    type: 'all',
					    text: 'All'
					}
				],
				buttonPosition: {
					align: 'right'
				}
		  	},
			series: [{
				data: chartData
			}]
		};

		return (
			<div className="container-fluid p-0">
				<div className="row no-gutters">
					<div className="col-md-6 offset-md-3 mt-3">
						<ReactHighcharts config = {config}/>
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
